a {
  text-decoration: none;
}

.std-margin {
  margin: 24px;
}

@-webkit-keyframes fadeIn {
  0% {opacity: 0;
    -webkit-transform: scale(.8, .9);
            transform: scale(.8, .9)}
  100% {opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);}
}

@keyframes fadeIn {
  0% {opacity: 0;
    -webkit-transform: scale(.8, .9);
            transform: scale(.8, .9)}
  100% {opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);}
}

body {min-height: 100vh}


